import { ConnectService } from './../../services/connect.service';
import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { requiredFields, WebhookSimulation } from '../../models/fasten/webhook-simulate-payload';
import { PortalConfigService } from '../../services/portal-config.service';
import { EHIExportSuccessPayload, EHIExportFailedPayload, ConnectionSuccessPayload, AuthorizationRevokedPayload, WebhookTestPayload } from '../../models/fasten/webhook-simulate-payload';

@Component({
    selector: 'app-org-webhook-simulate',
    templateUrl: './org-webhook-simulate.component.html',
    styleUrls: ['./org-webhook-simulate.component.scss']

})
export class OrgWebhookSimulateComponent implements OnInit {
    @Input() webhookId!: string;
    type: string | null = null;
    payload: string = '';
    submitLoading = false;
    submitError: string | null = null;
    submitSuccess: string | null = null;

    eventTypes = [
        { label: 'Patient EHI Export Success', value: 'patient.ehi_export_success' },
        { label: 'Patient EHI Export Failed', value: 'patient.ehi_export_failed' },
        { label: 'Patient Connection Success (beta)', value: 'patient.connection_success' },
        { label: 'Patient Authorization Revoked (beta)', value: 'patient.authorization_revoked' },
        { label: 'Webhook Test', value: 'webhook.test' }
    ];

    defaultPayloads: Record<string, any> = {
        'patient.ehi_export_success': new EHIExportSuccessPayload(),
        'patient.connection_success': new ConnectionSuccessPayload(),
        'patient.authorization_revoked': new AuthorizationRevokedPayload(),
        'patient.ehi_export_failed': new EHIExportFailedPayload(),
        'webhook.test': new WebhookTestPayload(),
    };
    requiredFields = requiredFields;

    constructor(
        public activeModal: NgbActiveModal,
        public portalConfig: PortalConfigService,
        private connectService: ConnectService,
    ) { }

    ngOnInit(): void {
    }

    onTypeChange() {
        if (this.type) {
            this.payload = JSON.stringify(this.defaultPayloads[this.type], null, 2);
        }
    }

    submitWebhookSimulation() {
        this.submitLoading = true;
        this.submitError = null;

        if (!this.webhookId) {
            this.submitLoading = false;
            this.submitError = 'Webhook ID not found in route';
            return;
        }

        try {
            const parsedPayload = JSON.parse(this.payload);
            if (this.type && this.requiredFields[this.type]) {
                const missingFields = this.requiredFields[this.type].filter(
                    field => !(field in parsedPayload) || parsedPayload[field] === ''
                );

                if (missingFields.length > 0) {
                    this.submitLoading = false;
                    this.submitError = `Missing required field(s): ${missingFields.join(', ')}`;
                    return;
                }
            }

            const simulation: WebhookSimulation = {
                type: this.type!,
                payload: parsedPayload
            };

            this.submitLoading = true;
            const orgId = this.portalConfig.config$.user?.org_id!;
            this.connectService.simulateWebhook(orgId, this.webhookId, simulation).subscribe({
                next: (response) => {
                    this.submitLoading = false;

                    if (response.success) {
                        this.submitError = '';
                        this.submitSuccess = 'Simulation successful!';
                        this.activeModal.close(response.data);
                    } else {
                        this.submitError = `Simulation failed: ${response.error}`;
                        this.submitSuccess = '';
                    }
                },
                error: (err) => {
                    this.submitLoading = false;
                    const backendMessage = err?.error?.error;
                    this.submitError = `Simulation failed: ${backendMessage || err.statusText || 'Server error'}`;
                    this.submitSuccess = '';
                }
            });
        } catch {
            this.submitLoading = false;
            this.submitError = 'Invalid JSON payload';
        }

    }

    getFieldsForType(type: string | null) {
        if (!type) return [];

        const defaults = this.defaultPayloads[type] || {};
        const required = new Set(this.requiredFields[type] || []);

        return Object.keys(defaults).map(key => ({
            name: key,
            type: typeof defaults[key],
            required: required.has(key)
        }));
    }
}
