import { Directive } from '@angular/core';
import {AbstractControl, NG_VALIDATORS, ValidationErrors, Validator} from "@angular/forms";

@Directive({
  selector: '[emailLowercaseValidator]',
  providers: [
    {
      provide: NG_VALIDATORS,
      useExisting: EmailLowercaseValidatorDirective,
      multi: true,
    },
  ],
  standalone: true,
})
export class EmailLowercaseValidatorDirective implements Validator {
  validate(control: AbstractControl): ValidationErrors | null {
    return control.value && control.value.toLowerCase() != control.value
      ? { email_lowercase: true }
      : null;
  }

}
