import {ResponseWrapper} from "../app/models/response-wrapper";

export function parseHttpErrorResponse(resp: any): ResponseWrapper {
  if (resp?.error && resp.error) {
    // A client-side or network error occurred. Handle it accordingly.
    return resp?.error as ResponseWrapper
  } else {
   return { data: null, success: false, error: "An unknown error occurred."}
  }
}
