import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';

type Timeframe = 'week' | 'month';

type OutcomeKey = 'missing' | 'rejected' | 'successful';

interface FilterDefinition {
  key:
    | 'org_id'
    | 'api_mode'
    | 'brand_id'
    | 'portal_id'
    | 'endpoint_id'
    | 'tefca_root_id';
  label: string;
  placeholder: string;
  options: string[];
}

interface DailyOutcome {
  day: string;
  missing: number;
  rejected: number;
  successful: number;
}

interface RequestMetric {
  platform: string;
  daily: DailyOutcome[];
}

interface RequestRow {
  platform: string;
  totals: Record<OutcomeKey, number>;
  daily: Record<string, DailyOutcome>;
}

interface ResourceBreakdown {
  platform: string;
  resources: { name: string; count: number }[];
}

