@Component({
  selector: 'app-collect-metrics',
  templateUrl: './collect-metrics.component.html',
  styleUrls: ['./collect-metrics.component.scss']
})
export class CollectMetricsComponent implements OnInit {
  filterForm: FormGroup;
  filters: FilterDefinition[] = [
    {
      key: 'org_id',
      label: 'Org ID',
      placeholder: 'All orgs',
      options: ['fasten-pilot', 'fasten-production', 'fasten-sandbox']
    },
    {
      key: 'api_mode',
      label: 'API Mode',
      placeholder: 'Any mode',
      options: ['sandbox', 'production']
    },
    {
      key: 'brand_id',
      label: 'Brand',
      placeholder: 'Any brand',
      options: ['Acme Health', 'Bright Medical', 'CareNow', 'OneHealth']
    },
    {
      key: 'portal_id',
      label: 'Portal',
      placeholder: 'Any portal',
      options: ['consumer-portal', 'provider-portal']
    },
    {
      key: 'endpoint_id',
      label: 'Endpoint',
      placeholder: 'Any endpoint',
      options: ['ehr-primary', 'ehr-secondary', 'direct-mailbox']
    },
    {
      key: 'tefca_root_id',
      label: 'TEFCA Root',
      placeholder: 'Any root',
      options: ['tefca-east', 'tefca-west']
    },
  ];

  requestRows: RequestRow[] = [];
  daysOfWeek: string[] = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];

  resourceTimeframe: Timeframe = 'week';
  resourceBreakdowns: ResourceBreakdown[] = [];

  constructor(private readonly fb: FormBuilder) {
    const controls = this.filters.reduce<Record<FilterDefinition['key'], string>>((acc, filter) => {
      acc[filter.key] = '';
      return acc;
    }, {} as Record<FilterDefinition['key'], string>);

    this.filterForm = this.fb.group(controls);
  }

  ngOnInit(): void {
    this.requestRows = this.buildRequestRows(REQUEST_METRICS);
    this.resourceBreakdowns = this.getResourceData(this.resourceTimeframe);
  }

  setResourceTimeframe(timeframe: Timeframe): void {
    if (this.resourceTimeframe === timeframe) {
      return;
    }

    this.resourceTimeframe = timeframe;
    this.resourceBreakdowns = this.getResourceData(timeframe);
  }

  outcomeTotal(row: RequestRow, key: OutcomeKey): number {
    return row.totals[key];
  }

  outcomeForDay(row: RequestRow, day: string, key: OutcomeKey): number {
    const entry = row.daily[day];
    return entry ? entry[key] : 0;
  }

  private buildRequestRows(metrics: RequestMetric[]): RequestRow[] {
    return metrics.map((metric) => {
      const totals = metric.daily.reduce<Record<OutcomeKey, number>>((acc, item) => {
        acc.missing += item.missing;
        acc.rejected += item.rejected;
        acc.successful += item.successful;
        return acc;
      }, { missing: 0, rejected: 0, successful: 0 });

      const daily = metric.daily.reduce<Record<string, DailyOutcome>>((acc, item) => {
        acc[item.day] = item;
        return acc;
      }, {});

      return {
        platform: metric.platform,
        totals,
        daily,
      };
    });
  }

  private getResourceData(timeframe: Timeframe): ResourceBreakdown[] {
    return timeframe === 'week' ? RESOURCE_WEEKLY : RESOURCE_MONTHLY;
  }
}
