import {Component, Input, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, FormArray, Validators} from '@angular/forms';
import {ConnectService} from '../../services/connect.service';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {forkJoin} from 'rxjs';

@Component({
  selector: 'app-org-team-invite-multiple',
  templateUrl: './org-team-invite-multiple.component.html',
  styleUrls: ['./org-team-invite-multiple.component.scss']
})
export class OrgTeamInviteMultipleComponent implements OnInit {
  @Input() orgId: string = '';

  inviteForm: FormGroup;
  submitOrgInviteLoading: boolean = false;
  submitOrgInviteError: string = '';

  constructor(
    private fb: FormBuilder,
    private connectService: ConnectService,
    public activeModal: NgbActiveModal
  ) {
    this.inviteForm = this.fb.group({
      emails: this.fb.array(
        Array(4).fill('').map(() => this.fb.control('', [Validators.email]))
      )
    });
  }

  ngOnInit(): void {}

  get emails(): FormArray {
    return this.inviteForm.get('emails') as FormArray;
  }

  sendOrgTeamInvites() {
    const emails = this.emails.value
      .map((e: string) => e.trim())
      .filter((e: string) => e.length > 0);

    if (emails.length === 0) {
      this.activeModal.close([]);
      return;
    }

    this.submitOrgInviteLoading = true;
    this.submitOrgInviteError = '';

    forkJoin(emails.map((email: string) => this.connectService.sendOrgInvite(this.orgId, email))).subscribe({
      next: () => {
        this.submitOrgInviteLoading = false;
        this.activeModal.close(emails);
      },
      error: (error) => {
        console.error('Error sending Org Invites', error);
        this.submitOrgInviteLoading = false;
        this.submitOrgInviteError = JSON.stringify(error);
      }
    });
  }
}
